// bootstrap overrides to use ems instead of pixels
@font-size-base: 1em;//14px;

// had to remove floor/ceil from these to get the correct font sizes in em

@font-size-large:         (@font-size-base * 1.25); // ~18px
@font-size-small:         (@font-size-base * 0.85); // ~12px

@font-size-h1:            (@font-size-base * 2.6); // ~36px
@font-size-h2:            (@font-size-base * 2.15); // ~30px
@font-size-h3:            (@font-size-base * 1.7); // ~24px
@font-size-h4:            (@font-size-base * 1.25); // ~18px
@font-size-h5:            @font-size-base;
@font-size-h6:            (@font-size-base * 0.85); // ~12px

@line-height-computed:    (16px * @line-height-base); // note force of 16px as we're now using em

.panel-title {
    font-size: (@font-size-base * 1.125); // remove floor/ceil
}
.lead {
    font-size: (@font-size-base * 1.15); // remove floor/ceil
}

